import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, Selic } from '../../../../models/dtos/mlf/selic';
import { MES_ENUM_VALUES } from '../../../../models/enumerated/mlf/mesEnum';

interface SelicEditViewProps extends EditViewProps<Selic> {}

const SelicEditView: FC<SelicEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<Selic>({ defaultValues: selectedItem, mode: 'onBlur' });

  const beforePersist = (formData: Selic): Selic => {
    formData.referencia = formProps.getValues().ano.concat(formProps.getValues().mesInt.toString());
    return formData;
  };

  const defaultWidth: number = 80;

  return (
    <WindowComponent title="Manutenção de Selic" wikiId={1009}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} beforePersist={beforePersist}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 1900, required: true, ...onlyNumber(0, 4) } }}
              id="ano"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Ano"
              labelWidth={defaultWidth}
              placeholder="Informe o ano da Selic"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="mesInt"
              inputDropDownProps={{
                optionsProvider: MES_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              isPrependLabel={true}
              label="Mês"
              labelWidth={defaultWidth}
              placeholder="Informe o mês da Selic"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0.01, max: 100, required: true } }}
              id="taxa"
              isPrependLabel={true}
              label="Taxa"
              labelWidth={defaultWidth}
              placeholder="Informe a taxa da Selic"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default SelicEditView;
